@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

$main-color: #FF6B6B;
$sub-color: #4ECDC4;
$font-color: #222222;
$font-sub-color: #555;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
button {
  font-family: inherit;
}


body {
  font-family: 'Pretendard', sans-serif;
  letter-spacing: -0.02em;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FAF9F6;
}
.app {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

// *********** category ************
.category {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  text-align: center;
  .title-wrap {
    h1 {
      font-size: 40px;
      color: $font-color;
    }
    h2 {
      font-size: 20px;
      font-weight: 600;
      color: $font-sub-color;
      margin-bottom: 0.5rem;
    }
  }
  .category-wrap {
    h2 {
      color: $main-color;
      font-size: 20px;
      font-weight: 400;
      margin-bottom: 20px;
      animation: title 0.7s infinite alternate;
    }
  }
  @keyframes title {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-5px);
    }
  }
  .categoryBtn-wrap {
    width: 100%;
    height: 350px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 10px;
    button {
      aspect-ratio: 1 / 1;
      border-radius: 20px;
      border: 2px solid #ffecec;
      background-color: #fff;
      color: $main-color;
      font-size: 30px;
      font-weight: 700;
      transition: 0.3s;
      cursor: pointer;
      &.focus {
        background-color: $main-color;
        color: #fff;
      }
      &:hover:not(.focus) {
        background-color: #fffafa;
      }
    }
  }
}

.btn-style {
  width: 350px;
  height: 3.2rem;
  background-color: $sub-color;
  color: #fff;
  border: none;
  border-radius: 50px;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.5s;
  &:hover {
    filter: brightness(0.9);
  }
}


// ************ Quizpage *************
.quiz-page {
  width: 350px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  text-align: center;
  .quiz-title {
    text-align: left;
    width: 100%;
    h2 {
      font-size: 30px;
      font-weight: 800;
      color: $font-sub-color;
    }
    p {
      font-size: 18px;
      font-weight: 600;
      line-height: 32px;
      margin-top: 10px;
      overflow-wrap: break-word;
    }
  }
  .choices {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 100%;
    button {
      width: 100%;
      height: 3.2rem;
      background-color: #fff;
      color: $main-color;
      font-size: 18px;
      font-weight: 700;
      border: 2px solid #ffecec;
      border-radius: 20px;
      transition: 0.5s;
      cursor: pointer;
      &.focus {
        background-color: $main-color;
        color: #fff;
      }
      &:hover:not(.focus) {
        background-color: #fffafa;
      }
    }
  }
}


// ************ Result *************
.header-title {
  font-size: 30px;
  font-weight: 300;
}
.result {
  width: 100%;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 38px;
  align-items: center;
  text-align: center;
  font-weight: 700;
  .result-title {
    p {
      font-size: 35px;
      font-weight: 500;
      line-height: 1.8;
    }
    h2 {
      font-size: 40px;
      color: $font-color;
    }
  }
  h1 {
    margin: 2rem 0;
    font-size: 80px;
    color: $sub-color;
    animation: score 1.5s;
  }
  h3 {
    font-size: 25px;
    font-weight: 500;
    color: $font-color;
    animation: score 1.5s;
    span {
      font-weight: 700;
    }
  }
  .result-btn {
    width: 100%;
    height: 68px;
    margin-top: 20px;
    background-color: #fff;
    color: $sub-color;
    border: 2px solid $sub-color;
    border-radius: 50px;
    font-size: 23px;
    font-weight: 700;
    cursor: pointer;
    transition: 0.5s;
    &:hover {
      background-color: $sub-color;
      color: #fff;
    }
  }
}
@keyframes score {
  0% {
    transform: translateY(-30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}